@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  overflow-x: hidden;
}

.login-container {
  display: flex;
  min-height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: white;
}
.login-container h2 {
  font-size: 2rem;
  font-weight: 200;
  letter-spacing: 1px;
  font-family: "Open Sans", sans-serif;
  margin: 10px;
}
.sign-in-form {
  border-radius: 6px;
  background-color: white;
  color: rgb(86, 144, 195);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0rem 4rem;
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
}
.input-field {
  height: 30px;
  width: 200px;
}
.error {
  color: red;
}
.landing-main {
  padding: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.landing-content {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 10px;
}

@media (max-width: 570px) {
  .sign-in-form {
    padding: 5px;
  }
  .login-container h2 {
    font-size: 1.5rem;
    font-weight: 200;
  }
  .landing-main {
    padding: 5%;
  }
  .landing-content {
    width: 100%;
  }
}

